  .btn.btn-fill {
    background: #fff;
  }
  
  .btn.btn-fill {
    color: #000;
  }
  
  .btn {
    border-width: 1px;
    border-style: solid;
    border-radius: 0px;
    cursor: pointer;
    padding: 10px 22px;
    display: inline-block;
    text-transform: uppercase;
    letter-spacing: 2px;
    position: relative;
    -webkit-transition: all 0.4s ease-out 0s;
    transition: all 0.4s ease-out 0s;
    color: #fff;
  }
  
  section {
    background: #FFF;
    padding-top: 115px;
    padding-bottom: 90px;
  }
  
  .base-header {
    padding-bottom: 56px;
    width: 57%;
    margin: auto;
    text-align: center;
  }
  
  .base-header small {
    font-size: 18px;
    color: #9dca00;
    text-transform: capitalize;
  }
  
  .base-header h3 {
    padding-bottom: 16px;
    position: relative;
    text-transform: capitalize;
    font-weight: 700;
    margin-bottom: 0px;
    letter-spacing: .5px;
    font-size: 40px;
    line-height: 50px;
    color: #2b343b;
    margin-top: 5px;
  }
  
  .base-header h3::before {
    position: absolute;
    content: "";
    background-color: #aedf04;
  }
  
  .base-header h3::before,
  .base-header h3::after {
    position: absolute;
    content: "";
    background-color: #b9ea0f;
    width: 15px;
    height: 11px;
    bottom: 0;
    left: 50%;
    margin-left: -1px;
    border-radius: 136px;
    margin-bottom: -6px;
  }
  
  .base-header h3::after {
    margin-left: -12px;
    background-color: #9dca00;
  }
  
  .base-header.base_header_left {
    width: 100%;
    text-align: left;
    padding-bottom: 40px;
  }
  
  .base-header.base_header_left h3::after,
  .base-header.base_header_left  h3::before {
    left: 0%;
    margin-left: 0px;
  }
  
  .base-header.base_header_left h3::before {
    margin-left: 10px;
  }
  
  #preloader {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999999;
    width: 100%;
    height: 100%;
    overflow: visible;
    background: #fff url("/assets/images/pre_loader.gif") no-repeat center center;
    color: #000;
  }
  
  .more-link:before {
    width: 100%;
    height: 0;
    top: 50%;
    left: 50%;
    background: #2b343b;
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
            transform: translateX(-50%) translateY(-50%) rotate(45deg);
    content: '';
    position: absolute;
    z-index: -1;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }
  
  .more-link:hover::before {
    height: 400%;
    opacity: 1;
  }
  
  .more-link:hover {
    color: #fff;
  }
  
  .more-link {
    display: inline-block;
    font-size: 16px;
    padding: 12px 35px 13px 35px;
    color: #fff;
    text-transform: capitalize;
    font-weight: 500;
  }
  
  .more-link {
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent;
    position: relative;
    overflow: hidden;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    /*background: #9dca00;*/
    background: #9dca00;
    border-radius: 2px;
  }
  
  .owl-prev, .owl-next {
    -webkit-transition: all 0.4s linear 0s;
    transition: all 0.4s linear 0s;
    position: absolute;
    top: 50%;
    margin-top: -25px;
    z-index: 99;
    width: 36px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    color: #333;
    opacity: 0;
  }
  
  .owl-theme .owl-controls .owl-nav div {
    padding-right: 20px;
  }
  
