   .coupon {
       background: #f0f0f0 none repeat scroll 0 0;
       border: medium none;
       height: 35px;
       margin-bottom: 20px;
       padding: 10px;
       width: 100%;
   }
   a.app-coupon,
   a.get-quote {
       background: #2b343b;
       color: #fff;
       display: block;
       font-size: 14px;
       font-weight: 500;
       height: 40px;
       line-height: 40px;
       padding: 0 20px;
       text-align: center;
       text-transform: capitalize;
       width: 36%;
   }
   a.app-coupon:hover,
   a.get-quote:hover {
       /*! background: #ffcd23; */
   }
   p.title-country,
   p.title-state,
   p.p-code {
       color: #000;
       font-size: 14px;
       font-weight: 400;
       padding-top: 10px;
       text-transform: capitalize;
   }
   .sort-by-country,
   .sort-by-state,
   .post-code {
       -moz-appearance: none;
       background: #f0f0f0;
       border: medium none;
       height: 37px;
       padding: 0 10px;
       width: 100%;
       color: #333;
   }
   p.title-country { 
       padding-top: 3px; 
       margin-bottom: 10px;
   }
   .estimate-ship .postal-code {
       margin-bottom: 20px;
   }
   
   .estimate-ship div {
      margin-bottom: 8px;
  }
   .grand-total-area {
       background: #f5f5f5 none repeat scroll 0 0;
       padding: 35px 35px;
   }
    .grand-total-area h4 {
   
    margin-bottom: 34px;
   }
   .grand-total-area span { 
      float: right;
  }
   .grand-total-area p{
       color: #333;
       display: block;
       font-size: 18px;
       text-align: left;
       text-transform: capitalize;
       width: 100%;
   }
   .grand-total-area .grand-total {
       border-top: 1px solid #d2cccc;
       color: #333;
       font-size: 24px;
       padding-bottom: 0px;
       text-align: left;
       text-transform: capitalize;
       margin-top: 15px;
       padding-top: 15px;
   }
   .grand-total span.amt {
       color: #9dca00;
   }
   .pro-checkout{
       background: #2b343b;
       color: #fff;
       display: block;
       font-size: 15px;
       font-weight: 500;
       height: 40px;
       line-height: 40px;
       margin-top: 30px;
       padding-bottom: 0;
       padding-left: 30px;
       padding-right: 30px;
       text-align: center;
       width: 100%;
   }
    .pro-checkout:hover{ 
       color: #fff; 
   }
